@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
}

html, body{
    height: 100%;
}

a{
    color: inherit;
    text-decoration: none;
}

button{
    cursor: pointer;
    outline: none;
    border: 0;
}
