@import '../base/colors';

.note{
	width: 100%;
	height: 100%;
	padding: 10px;
	display: flex;
	flex-wrap: wrap;
	list-style-type: none;
	color: $dark-green;

	&__single{
		width: calc(100%/3.1);
		max-width: 300px;
		min-width: 220px;
		height: 250px;
		padding: 10px;
		background: $beige;
	}

	&__title{
		@extend .news__title;
		width: fit-content;
		cursor: pointer;
	}

	&__modal{
		position: absolute;	
		left: 0;
		top: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		background: #00000066;
		width: 100%;
		height: 100%;
		visibility: hidden;

		&.note__modal--visible{
			visibility: visible;
		}
	}

	&__body{
		width: 98%;
		max-width: 500px;
		height: 90%;
		max-height: 450px;
		background: $beige;
		padding: 10px;

		h2{
			cursor: initial;
		}
	}

	&__content-wrapper{
		width: 100%;
		height: calc(100% - 39px);
		overflow: auto;
	}
}